import './index.scss';

import Vue from 'vue';

Vue.component('pk-step', {
	props: {
		// 00
		current: {
			type: Number,
			default: 0
		},
		// circle,arrow,dotcircle
		type: {
			type: String,
			default: 'circle'
		},
		// 
		class: { type: String }
	},
	template: '<div class="pk-step" :class="\'pk-step-\'+type"><slot></slot></div>'
});



